/* AniButtons */
.anibutton, .buymodal__link button {
  position: relative;
  overflow: hidden;
  transition: 0.3s;
}

.anibutton:after, .buymodal__link button:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  height: 100%;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.1) 10%, rgba(255, 255, 255, 0.2) 20%, rgba(255, 255, 255, 0.6));
  width: 20px;
  transform: skewX(-45deg);
  left: -20%;
  transition: all .6s ease;
  animation-name: blick;
  animation-duration: 6s;
  animation-iteration-count: infinite;
}

@keyframes blick {
  15%, to {
    left: 110%;
  }
}

.playbutton {
  width: 70px;
  height: 70px;
  display: block;
  position: relative;
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.5);
  background: url(../img/play.svg) no-repeat;
  background-size: 27px;
  background-position: 59% 53%;
  z-index: 1;
  transition: all 0.3s;
}

.playbutton:hover {
  opacity: 0.7;
}

.playbutton:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.3);
  animation: play 2s infinite;
}

@keyframes play {
  0% {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(3.0883);
    transform: scale(3.0883);
  }
}

.uk-button-default:disabled, .uk-button-primary:disabled, .uk-button-secondary:disabled, .uk-button-danger:disabled {
  background-color: #bcbcbc;
  box-shadow: 0 10px 30px 0px rgba(188, 188, 188, 0.5);
  color: #999;
  border-color: #e5e5e5;
}

[v-cloak] {
  display: none;
}

